*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  
  .header{
    display: flex;
    justify-content: space-around;
    align-items: center;
    min-height: 10vh;
    background-color: #DC143C;          /*#5d4954 #a71186*/
    font-family: 'Poppins', sans-serif;
  }
  
  .logo{
    color: aqua;
    text-transform: uppercase;
    letter-spacing: 5px;
    font-size: 20px;
  }
  
  .nav-links {
    display: flex;
    justify-content: space-around;
    min-width: 40%;
  }
  .nav-links li{
    list-style:none;
  }
  
  .nav-links a{
    color: yellow;
    font-size: 15px;
    text-decoration: none;
    text-transform: uppercase;
    letter-spacing: 2px;
  }
  
  @media screen and (max-width: 700px){
    .nav-links {
      display :none;
    }
  }

                
.main-intro {
    padding: 0;
    margin: 0;
    height: 600px;
    background-color: #252627; 
    display: grid; 
    grid-template-columns: [col1] 1fr [col2] 1fr [col1end]; 
    grid-template-rows: auto;
    grid-template-areas: 
        "writing image";
    
}

.text {
    grid-area: writing;
    place-self: center;
}

.text h1{
    color: black;
    font-size: 65px;
    font-family: Arial, Helvetica, sans-serif;
    color: antiquewhite;
}
/*
<a className = "contact-me" href = "#footer-text">Let's get in touch!</a>*/
.text span{
    display: inline-block;
}

.move {
    margin-left: 5px;
    padding: 0px;
}

.name {
    margin: 0;
    padding-right: 4px;
}

.firstletter {
    padding-left: 10px;
}

.surname {
    padding-left: 4px;
}

span:hover {
    color: aqua;
    transition: all .7s;
}

/*time for student / programmer / baker / artist */
.text p {
    font-size: 20px;
    color: var(--yellow);
    word-spacing: 10px;
    letter-spacing: 2px;
    padding-top: 5px;
}

