.myButton {
	box-shadow: 3px 4px 0px 0px #dceb0c;
	background:linear-gradient(to bottom, #7af6ff 5%, #e00d62 100%);
	background-color:#7af6ff;
	border-radius:5px;
	border:1px solid #9e0f81;
	display:inline-block;
	cursor:pointer;
	color:#000000;
	font-family: 'Quicksand' , sans-serif;
	font-size:20px;
	font-weight:bold;
	padding:12px 44px;
	text-decoration:none;
	text-shadow:0px 1px 0px #9603e0;
}
.myButton:hover {
	background:linear-gradient(to bottom, #e00d62 5%, #7af6ff 100%);
	background-color:#e00d62;
}
.myButton:active {
	position:relative;
	top:1px;
}

