.image img {
    width: 450px;
    height: 450px;
    border: 5px solid var(--crimson);
    border-radius: 25px;
}

.image {
    grid-area: image;
    place-self: center;
}
.aboutMe {
    background-color: var(--crimson);
    height: 500px;
    display: grid;
    grid-template-columns: [col1] 37% [col2] 2% [col3] 5% [col4] auto [col5];
    grid-template-rows: [row1] 10% [row2] 80% [row3] 10%;
    grid-template-areas: 
        ". . . ."
        "vishay vertical . varnan"
        ". . . .";
}

.vishay {
    grid-area: vishay;
    font-family: 'Dancing Script', cursive;
    color: yellow;
    justify-self: center;
    align-self: center;
    font-size: 60px;
}

.first {
    padding-left: 30px;
}

.second {
    padding-left: 80px;
}

.vertical {
    border-right: 2px solid rgba(0,0,0,0.8);
    grid-area: vertical;
}

.desc {
    grid-area: varnan;
    justify-self: center;
    align-self: center;
}

.desc a {
    text-decoration: none;
    color: white;
}
.desc a:hover {
    text-decoration: underline;
}
.desc p {
    padding: 10px 10px;
    color: aqua;
    background-color: #252627;
    font-family: 'Roboto', sans-serif;
    box-shadow: 14px 12px 10px 3px #0872db;
    line-height: 30px;
    font-weight: 500;
    font-size: 18px;
    border-radius: 15px;
    width: 80%;
    padding-left: 30px;
    
    
    

}
.myskills{
    background-color: #252627;
    display: grid;
    grid-template-columns: [col1] 70% [col2] 30% [col3];
    grid-template-rows: 80% 20%;
    grid-template-areas: 
        "actual writing"
        "button .";
    height: 600px;
}

.writing{
    grid-area: writing;
    font-family: "Dancing Script", cursive;
    color: yellow;
    justify-self: start;
    align-self: center;
    font-size: 80px;
}

.actually {
    grid-area: actual;
}

.actually p {
    /*color: rgb(1, 181, 252);*/
    color: crimson;
    font-family: 'Vast Shadow', cursive;
    font-size: 30px;
    text-align: center;
    padding-top: 5px;
    padding-right: 30px;
}
.container {
    width:75%;
    padding-left: 100px;
}

